import * as React from 'react';
import { Fragment, useState } from 'react';
import { useCreateCommunity } from '../assets/helper/index';
import Box from '../components/Box';
import Form from '../components/Form';

export default (props) => {
    const [boarding, setOnboarding] = useState(1);
    const [communityName, setcommunityName] = useState('');
    const [communitySymbol, setcommunitySymbol] = useState('');

    if (boarding === 3) {
        const { community } = useCreateCommunity({}, communityName, communitySymbol);
        return community;
    };

    return (
        <Fragment>
            {
                boarding === 1 && (
                    <Fragment>
                        <Box title="Create a community name">
                            <Form
                                title='Enter community name'
                                onSubmit={(text: any) => {
                                    setcommunityName(text)
                                    setOnboarding(2)
                                }}
                                onClick={() => setOnboarding(2)}
                            />
                        </Box>
                    </Fragment>
                )
            }

            {
                boarding === 2 && (
                    <Fragment>
                        <Box title="Create a community Symbol">
                            <Form
                                title='Enter community Symbol'
                                onSubmit={(text: any) => {
                                    setcommunitySymbol(text)
                                    setOnboarding(3)
                                    useCreateCommunity({}, communityName, communitySymbol);
                                }}
                                onClick={() => setOnboarding(3)}
                            />
                        </Box>
                    </Fragment>
                )
            }

            {
                boarding === 3 &&
                (
                    <Fragment>
                        <Box title={`Community Name: ${communityName}`} >
                            <h3 style={{ color: 'black' }} >
                                Community Symbol: {communitySymbol}
                            </h3>
                        </Box>
                    </Fragment>
                )
            }
        </Fragment>
    )
}